/* The side navigation menu */

.sidenav {
  height: 100%; /* 100% Full-height */
  width: 270px; /* 0 width - change this with JavaScript */
  margin-left: 10%;
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  /* background-color: rgba(155, 26, 26, 0.02);  */
  background-color: #5655da;
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
  padding: 40px 0 0 30px;
}

/* The navigation menu links */

.sidenav a {
  padding: 8px 8px 8px 0px;
  text-decoration: none;
  font-size: 15px;
  color: #d8d8e9;
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

/* When you mouse over the navigation links, change their color */

.sidenav a:hover {
  color: #f74470
}

.sidenav a.active{
  color: #f74470;
  background-color: white;
  border-top-left-radius: 15px;
  border-bottom-left-radius:15px;
}

.avatar{
  width: 135px;
  height: 135px;
  border-radius: 50%;
  margin: 0 auto;
}

.avatar img{
  width: 100%;
  border-radius: 50%;
}

.name {
  color: white;
}

.name h1{
  font-size: 1.7em;
  margin: 20px auto 5px auto;
}

.contact{
  font-size: 12px;
  color:white;
  margin-top: 20px;
}

.contact h3 {
  margin-bottom: 0;
}

.contact ul {
  list-style-type: none;
  padding:0;
  margin: 0;
}

.contact ul li {
  padding: 5px 0 5px 0;
}

.social-list {
  color:white;
  margin: 20px auto;
  margin-left: -6px;
}

.social-list a {
  display: inline-block;
}

/* Mobile navigation button */

#mobile-nav {
  background:rgba(12, 12, 12,0.5);
  height:40px;
  width: 40px;
  position:absolute;
  right: -40px;
  z-index: 2;
  margin-left: 0;
  display: none;
  top:50px;
}

#mobile-nav span{
  display: block;
  position: absolute;
  height: 3px;
  width: 60%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 18%;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

#mobile-nav span:nth-child(1) {
  top: 13px;
}

#mobile-nav span:nth-child(2), #mobile-nav span:nth-child(3) {
  top: 28px;
}

#mobile-nav span:nth-child(4) {
  top: 43px;
}

#mobile-nav.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#mobile-nav.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

#mobile-nav.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#mobile-nav.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

