/*
  Navigation
*/

ul#nav{
  position: relative;
  list-style-type: none;
  padding-left: 0;
}

ul#nav li a {
  position: relative;
  padding-left:10px;
}

ul#nav li a.active::before{
  content: '';
  position: absolute;
  right:0;
  top:-30px;
  width: 30px;
  height:30px;
  background: #5655da;
  border-radius: 50%;
  box-shadow: 15px 15px 0 white;
}

ul#nav li a.active::after{
  content: '';
  position: absolute;
  right:0;
  bottom:-30px;
  width: 30px;
  height:30px;
  background: #5655da;
  border-radius: 50%;
  box-shadow: 15px -15px 0 white;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */

@media screen and (max-width: 991px) {
  .sidenav { margin-left: 0; -webkit-transform: translateX(-270px); transform: translateX(-270px);
  }

  .sidenav a {
    font-size: 18px;
  }

  #mobile-nav {
    display: block;
  }

  .pushed {
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }

  #main{
    margin-left: 0;
    width: 100%;
  }

  .main-pushed{
    -webkit-transform:translateX(270px);
    transform:translateX(270px)
  }
}

